body {
  text-align: center;
  background-color: rgb(13, 13, 15);
}

.container {
  background-color: rgb(178, 177, 175);
  min-height: 80vh;
  max-width: 50%;
  margin: 0 auto;
  background: url(https://media.istockphoto.com/id/1371224989/vector/blue-and-orange-intersecting-circles-geometric-element-business-circle-vector-illustration.jpg?s=612x612&w=0&k=20&c=YSs6c1U4Til_qqO9lFRKbJf0h8NEO43iCw5SxCUvKsk=);
  background-position: top;
  background-size: cover;
  background-attachment: fixed;

}

#calc {
 display: flex;
 justify-content: space-between;
 margin: 101px 0px 44px 32px;
 padding: 19px
}

#btn {
  float: right;
  margin: 25px 47px 0px 0;
  padding: 10px 30px;
  border-radius: 31px;
}
 #btn1{
  margin: 0px 77px 0px 85px;
    width: 83%;
    height: 41px;
} 
#display{
  text-align: right;
}
#btn-sec button{
  margin: 5px;
  background: url(https://media.istockphoto.com/id/1248542684/vector/abstract-blurred-colorful-background.jpg?s=612x612&w=0&k=20&c=6aJX8oyUBsSBZFQUCJDP7KZ1y4vrf-wEH_SJsuq7B5I=);
  background-position: top;
  background-size: cover;
  background-attachment: fixed;
  padding: 10px;
  border: none;
  box-shadow: 4px 0px 19px red;
  width: 100%;
  color: #ffffff;
transition: .5s ease-in-out  ;
}
#btn-sec p{
  display: flex;
}
